
.topbar {
    z-index: 2;
    width: 80%;
    height: 40px;

    margin: 0 auto;
    margin-top: 40px;
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 25px;
}

.topbar__text {
    z-index: 2;
    padding: 10px;
    text-align: center;

    position: relative;
    bottom: 5px;
}

.topbar__text h1 {
    text-transform: uppercase;
    color: #ffd76a;
    text-shadow: 1.5px 0px #cc9f1d;
    letter-spacing: .15em;


}

.topbar__bar {
    z-index: 2;
    justify-self: center;
    background-color: #929487;
    height: 80%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    border: 0px;
    border-radius: 50px;
    
}

.topbar__link {
    text-align: center;
    width: 100%;
    padding: 10px;
    text-decoration: none;
    text-transform: uppercase;

    color: #F2FFF6;
}

.link__text {
    font-weight: 500;
    letter-spacing: .1em;
}

.topbar__link:hover {
    color: #4d503d;
}

@media only screen and (max-width: 1600px) {


.topbar {
    z-index: 5;
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr;
    grid-gap: 25px;
    
}

.topbar__bar {
    /* width: minmax(200px, 10%); */
    width: 50%;
    min-width: 320px;
    
}



}
* {
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
  }
  
  html, body {
    background-color:#f9f6ef;
    height: 100%;
    margin: 0;
  }
  
  p {
    margin: 0px;
    font-size: 18px;
    font-family: "gill-sans-nova", sans-serif;
  }

  h1 {
    margin: 0px;
    font-family: "minerva-modern", sans-serif;
    font-weight: 700;
  }

  h2 {
    font-family: "gill-sans-nova", sans-serif;
    font-weight: 500;
    font-size: 64px;
    margin: 0px;
  }

  h3 {
    font-family: "gill-sans-nova", sans-serif;
    font-weight: 500;
    font-size: 36px;
    margin: 0px;
  }

  h4 {
    font-family: "minerva-modern", sans-serif;
    font-weight: 400;
    font-size: 21px;
    margin: 0px;
  }
  
  ol, ul {
    list-style: none;
  }
  
  ul {
    padding-left: 0px;
  }

  strong {
    font-family: "gill-sans-nova";
  }
  
  .page {
      height: 100%;
      background-color:#f9f6ef;
  }
  
  .page__container {
    background-color: #f9f6ef;
    width: 100%;
    min-width: 300px;
    height: 100%;
    margin-top: 150px;
  
    display: grid;
    grid-template-columns: [left-gutter] 1fr [content] 8fr [right-gutter] 1fr;
  }

  .hidden {
    display: none;
  }
  
  .triangle {
    z-index: 1;
    position: absolute;
    top: 0px;
    width: 0;
    height: 0;
    border-top: 500px solid #f0e9d9;
    border-right: 500px solid transparent;
}



@media only screen and (max-width: 1280px) {

  .titletext {
    font-size: 75%;
  }
}
.abouttext {
  z-index: 2;
  grid-column: content;
  grid-row: 1 /2;
  width: 75%;
  height: 65%;
  padding: 25px;
  color: #8a7b72;
  background-color: #fafafa;
}


.abouttext p {
  letter-spacing: .07em;
}


  .titletext--about {
    grid-row: 2 / 3;
  }

.birdContainer {
    grid-column: content;
    display: grid;
    grid-template-rows: [header] 100px [modal_content] 1fr;
    grid-template-columns: [image] 1fr [info] 300px;
    grid-gap: 15px;
}

.birdName--normal {
    grid-column: image;
    grid-row: header;
    display: flex;
    align-items: flex-end;
    width: 90%;
}

.birdName--normal h2 {
    position: relative;
    left: 25px;
    color: #929487;
    letter-spacing: .1em;
}


.birdName--scientific {
    grid-column: info;
    grid-row: header;
    display: flex;
    align-items: flex-end;
}

.birdName--scientific h3 {
    color: #929487;
    font-style: italic;
    letter-spacing: .07em;
}




.birdInfo {
    grid-column: info;
    grid-row: modal_content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow: scroll;
    margin-top: 25px;
}



.birdInfo__facts {
    /* height: 300px; */
    height: auto;
    padding: 25px;
    padding-bottom: 50px;
    margin-bottom: 25px;
    background-color: #8a7b72;
    color: #fafafa;

    display: flex;
    flex-direction: column;
    
}

.birdInfo__facts p {
    font-size: 15px;
    letter-spacing: .07em;
}

.infoScroll {
    height: 300px;
    overflow: scroll;
}


.birdInfo__quote {
    padding: 25px;
}

.birdInfo__quote p {
    color: #8a7b72;
    font-size: 26px;
    font-weight: 300;
    font-style: italic;
    line-height: 1.35em;
    letter-spacing: .05em ;
    
}


.birdContainer--small {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
    /* width: 80%; */
    /* margin-left: 50px; */
}

.birdContainer--small h4 {
    color: #8590B8;
    letter-spacing: 0.1em;
    
}

.birdImg--small {
    text-align: center;
    overflow: hidden;
    height: 200px;
    margin-bottom: 10px;
    /* overflow: hidden; */
}

.birdImg--small img {
    transition: -webkit-transform .5s, -webkit-filter .05s ease-in-out;
    transition: transform .5s, filter .05s ease-in-out;
    transition: transform .5s, filter .05s ease-in-out, -webkit-transform .5s, -webkit-filter .05s ease-in-out;
}

.birdImg--small img:hover {
    
    -webkit-transform: scale(1.15);
    
            transform: scale(1.15);

}

.birdText {
    width: 93%;
    display: flex;
    align-items: left;
}


.page__container--birds {
    margin-top: 100px;
    
}


.imgContainer {
    z-index: 2;
    grid-column: content;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-auto-rows: 250px;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    margin-bottom: 100px;
}

.titletext--birds {
    position: relative;
    bottom: 85px;
}
